import { Injectable } from '@angular/core';
import {Http, Response} from "@angular/http";

import { AllRegions } from '../contentpage/allRegionData';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { dealerModel} from '../models/dealerModel'; 
 
const httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class DealerService {
  baseUrl:string ="https://dealerlocatorwapp.azurewebsites.net/api/";
  constructor(private http:HttpClient) {}
 
    // Uses http.get() to load data from a single API endpoint
    getAllDealers() {  
        return this.http.get<dealerModel[]>(this.baseUrl + 'dealer/getall');
    }
  
  getAllRegions(){
    return AllRegions
  }
}




