import { Component, OnInit, OnChanges, NgZone } from '@angular/core';
import { serviceTypeModel } from '../models/serviceTypeModel';
import { statesModel } from '../models/statesModel';
import { citiesModel } from '../models/citiesModel';
import { ServiceTypeService } from '../services/service-type.service';
import { DealerService } from '../services/dealer.service';
import { AllRegionsService } from '../services/all-regions.service';
import { allRegionsModel } from '../models/allRegionsModel';
import { filterModel } from '../models/filterModel';
import { DataServiceService } from '../services/data-service.service';



@Component({
  selector: 'app-contentpage',
  templateUrl: './contentpage.component.html',
  styleUrls: ['./contentpage.component.css'],
  providers: [ServiceTypeService, AllRegionsService]
})
export class ContentpageComponent implements OnInit, OnChanges {
  toggleDropdown: boolean = false;
  toggleFilters: boolean = false;

  serviceType: serviceTypeModel[];
  serviceType123: serviceTypeModel[];

  serchCountryCode: any;

  allcitystatecountry: any[];
  filteredStates: statesModel[] = [];
  filterdCities: any[] = [];
  allcities: any[];

  flagAdvanceSearch: boolean = true;
  isAdvFilter: boolean = true;
  dropdownList = [];
  selectedItems = [];

  dropdownSettings = {};
  dropdownSettingsStates = {};
  dropdownSettingsCities = {};



  public addrKeys: string[];
  public addr: object;

  private _allRegionsList: allRegionsModel[];

  searchText: any;

  userLoclat: any;
  userLoclng: any;

  public loadDataCheck: boolean = false;

  constructor(private serviceTypeService: ServiceTypeService,
    private _dealerService: DealerService,
    private _statescitiesService: AllRegionsService,
    private _dataService: DataServiceService,
    private zone: NgZone) {
    this._allRegionsList = _dealerService.getAllRegions();
    this.getAllRegions();

  }

  ngOnChanges() {
    this.loadDataCheck = false;
    this.dropdownSettingsStates = {
      singleSelection: false,
      idField: 'id',
      textField: 'name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      //itemsShowLimit: 3,
      enableSearchFilter: true,
    };
  }
  ngOnInit() {
    navigator.geolocation.getCurrentPosition((position) => {
      //console.log("Got position", position.coords);
      this.userLoclat = position.coords.latitude;
      this.userLoclng = position.coords.longitude;
      this.onSerchClick("", 1, 1, "", this.userLoclat, this.userLoclng);
      this.loadDataCheck = true;
    });


    this.dropdownSettings = {
      singleSelection: false,
      idField: 'id',
      textField: 'name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 3,
      enableSearchFilter: true,
      // allowSearchFilter: true
    };
    this.dropdownSettingsStates = {
      singleSelection: false,
      idField: 'id',
      textField: 'name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      //itemsShowLimit: 3,
      enableSearchFilter: true,
    };
    this.dropdownSettingsCities = {
      singleSelection: false,
      idField: '.code',
      textField: 'name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      enableSearchFilter: true,
    };
    this.serviceType = this.serviceTypeService.getAllServices();
  }

  getAllRegions() {
    this._statescitiesService.getAllRegions().subscribe((data: any[]) => {
      this.allcitystatecountry = data;
    });
  }
  onDropdownChange(country: any) {
    // this.filteredStates=[new statesModel('1','aa'),new statesModel('2','aqqqa')]; 
     
    const selectedCountry  = this._dataService.getSelectedCountries(country);
    var filteredCountryDetails= this._dataService.filterCountry(this.allcitystatecountry, selectedCountry);
    //var filteredCountryDetails = this.allcitystatecountry.filter(a => a.name == this.serchCountryCode);
     
    let count = 0;
    this.filteredStates=[];
    filteredCountryDetails[0].stateList.forEach(element => {
      this.filteredStates.push(new statesModel(element.code,element.name));
    });
    this.filterdCities=[];
    this.filterdCities = filteredCountryDetails[0].cityList;
  }


  onItemSelect(item: any) {
    console.log(item);
  }
  onSelectAll(items: any) {
    console.log(items);
  }

  toggle() {
    this.toggleDropdown = !this.toggleDropdown;
  }

  toggleFilter(type = false) {
    if (type) {
      this.isAdvFilter = !this.isAdvFilter;
    } else {
      this.flagAdvanceSearch = !this.flagAdvanceSearch;
      this.isAdvFilter = false;
    }
  }

  onSerchClick(searchText: any, region: any, distanceDropdown, dealerServices, userLoclat, userLoclng) {
    let data = new filterModel();
    data.inputText = searchText;
    data.region = region;
    data.distance = distanceDropdown;
    data.dealerServices = dealerServices;
    //alert('called');
    if (userLoclat && userLoclng) {
      data.userLat = this.userLoclat;
      data.userLong = this.userLoclng;
    }
    this.sendCheck();
    this.sendMessage(data);
    
  }
  sendMessage(filterData: filterModel): void {
    this._dataService.sendMessage(filterData);
  }

  sendCheck() {
    this._dataService.sendCheckIfLoadData(this.loadDataCheck);
  }

  setAddress(addrObj) {
    //debugger;   
    this.zone.run(() => {
      this.addr = addrObj;
      this.addrKeys = Object.keys(addrObj);
    });
  }

  errorHandler(err) {
    if (err.code == 1) {
      alert("Error: Access is denied!");
    } else if (err.code == 2) {
      alert("Error: Position is unavailable!");
    }
  }

}

